import React from 'react';
import NVD3Chart from 'react-nvd3';

function getDatum() {
  var urea = []
  for (var i = 0; i < 30; i++) {
    urea.push({
      x: i,
      y: Math.random() * (50 - 15) + 15
    });
  }
  return [
    {
      values: urea,
      key: 'Urea',
      color: '#04a9f5',
      area: true
    }
  ];
}

const LineChart = () => {
  const data = getDatum();
  return (
    <React.Fragment>
      {React.createElement(NVD3Chart, {
        xAxis: {
          tickFormat: function (d) {
            return d;
          },
          axisLabel: 'Mese'
        },
        yAxis: {
          axisLabel: 'mg/dl',
          tickFormat: function (d) {
            return parseFloat(d).toFixed(2);
          }
        },
        type: 'lineChart',
        datum: data,
        x: 'x',
        y: 'y',
        height: 300,
        renderEnd: function () {
          console.log('renderEnd');
        }
      })}
    </React.Fragment>
  );
};

export default LineChart;
